.heading {
    margin-bottom: 30px;
}
.subheading {
    font-size: 35px;
}

// Hero
.slip__hero {
    position: relative;
    background-color: white;
    overflow: hidden;

    @media only screen and (min-width: 1200px) {
        height: calc(100vh - var(--header-height));
}
}
.hero__content {
    height: 45%;
    color: var(--primary);
    text-align: center;
    margin: 100px auto 0;

    @media (min-width: 992px) {
        width: 755px;
    }
}
.hero__desc {
    
    @media (min-width: 992px) {
        width: 650px;
    }

    p {
        margin: 0;
    }
}
.hero__heading {
    color: inherit;
    line-height: 75px;
    margin-bottom: 1rem;
}

.hero__image {
    // margin-right: 85px;
    img {
        width: 362px;
        height: 474px;
        object-fit: contain;
    }
}

.hero__bg {
    width: 100%;
    height: 55%;
    background: url('../images/people.svg') repeat-x center;
    background-size: cover;
    margin-top: 2%;
}


// FAQ
.faq__title {
    text-align: center;
    width: 780px;
    margin: 0 auto;

    .heading {
        margin-bottom: 30px;
    }
}